/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: rgb(180, 180, 180) rgb(255, 255, 255);
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: rgb(231, 231, 231);
}

*::-webkit-scrollbar-thumb {
  background-color: rgb(180, 180, 180);
  border-radius: 20px;
  border: 3px solid rgb(255, 255, 255);
}
.App {
  padding: 40px;
  text-align: center;
  font-size: 26px;
}
.emoji__container {
  padding: 2rem;
  margin: 1rem auto;
  width: 400px;
  height: 200px;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: rgb(144, 144, 144);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.emoji__character {
  font-size: inherit;
  margin: 1rem;
}

.col {
  margin: 0 auto;
  max-width: 400px;
}
